.main {
  min-height: 100vh;
  background-color: #1d1d1d;

  &__content {
    display: flex;
    flex-direction: column;
    padding-block: 92px;
    row-gap: 92px;

    @include onTablet {
      padding-block: 120px;
      row-gap: 120px;
    }
  }
}

.container {
  @include contentPaddingInline;
}
